.HorizontalScroll {
  position: relative;

  /**
   *  WARNING 
   * `overflow-y`   , .. - `hidden`     .
   */
  overflow-x: hidden;
}

.HorizontalScroll__in {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;

  /**
   *     Firefox.
   *    64   ,     .
   */
  scrollbar-width: none;
}

.HorizontalScroll__in::-webkit-scrollbar {
  display: none;
}

.HorizontalScroll__in-wrapper {
  transition: transform 0.2s;
}

.HorizontalScroll__arrowLeft:hover ~ .HorizontalScroll__in .HorizontalScroll__in-wrapper {
  transform: translateX(8px);
}

.HorizontalScroll__arrowRight:hover ~ .HorizontalScroll__in .HorizontalScroll__in-wrapper {
  transform: translateX(-8px);
}

/**
 * CMP:
 * Tabs
 */
.Tabs .HorizontalScroll {
  min-width: 100%;
}

.Tabs__in .HorizontalScroll__in-wrapper {
  display: flex;
  align-items: stretch;
}

.Tabs--sizeX-compact.Tabs--withGaps .HorizontalScroll__in-wrapper::after,
.Tabs--sizeX-compact.Tabs--withGaps .HorizontalScroll__in-wrapper::before {
  display: block;
  width: 8px;
  flex-shrink: 0;
  height: 1px;
  content: '';
}

@media (--sizeX-compact) {
  .Tabs--sizeX-none.Tabs--withGaps .HorizontalScroll__in-wrapper::after,
  .Tabs--sizeX-none.Tabs--withGaps .HorizontalScroll__in-wrapper::before {
    display: block;
    width: 8px;
    flex-shrink: 0;
    height: 1px;
    content: '';
  }
}

